import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { API_URL } from "../../consts";

const initialState = {
    tasks : [],
    tasksCount : 0,
    tasksSubTasks: {}
}

export const getTasksThunk = createAsyncThunk(
    "history/getTasksThunk",
    async({skip,take, setCounter, counter})=>{
        const result = await fetch(`${API_URL}/api/tasks?skip=${skip}&take=${take}`)
        setCounter(--counter);
        return await result.json();
    }
)
export const getTasksSubTasksThunk = createAsyncThunk(
    "history/getTasksSubTasksThunk",
    async({skip,take, isValid, id})=>{
        const isValidStr = isValid === undefined ? "&isValid=false" : ""
        const result = await fetch(`${API_URL}/api/tasks/${id}?skip=${skip}&take=${take}${isValidStr}`)
        let json = await result.json();
        json.id = id;
        return json;
    }
)
export const  historySlice = createSlice({
    name: "history",
    initialState,
    ...initialState,
    reducers:{},
    extraReducers:(builder)=>{
        builder.addCase(getTasksThunk.fulfilled,(state, action)=>{
            state.tasks = action.payload.tasks;
            state.tasksCount = action.payload.count
        });
        builder.addCase(getTasksSubTasksThunk.fulfilled, (state, action)=>{
            state.tasksSubTasks[action.payload.id] = action.payload.subTasks
            console.log(123)
        });
    }

})

export const selectTasks = (state) => state.history.tasks
export const selectTasksSubTasks = (state) => state.history.tasksSubTasks
export const selectTasksCount = (state) => state.history.tasksCount

export default historySlice.reducer