import { useDispatch, useSelector } from "react-redux";
import { getTasksThunk, selectTasks, selectTasksCount } from "../../redux/check/historySlice";
import { useEffect, useState } from "react";
import SuccesCardForHistory from "./cards/SuccessCardForHistory";
import RunCard from "../check/cards/RunCard";
import { CardActions, Button } from "@mui/material";

export default function History(){
    const tasks = useSelector(selectTasks);
    const [skip, setSkip] = useState(0)
    const [counter, setCounter] = useState(-1000000)
    const tasksCount = useSelector(selectTasksCount)
    const take = 10;
    const dispatch = useDispatch();
    useEffect(()=>{
        setTimeout(()=>{
            dispatch(getTasksThunk({skip,take, setCounter:setCounter, counter}))
        },1000) 
    }, [counter, skip])
    return(
        <div>
            <h2  style={{marginLeft:"2%"}}>–ò—Å—Ç–æ—Ä–∏—è –æ–±—Ä–∞–±–æ—Ç–æ–∫</h2>
            {tasks.map(task=>{
                if(task.status==="done"){
                    return <SuccesCardForHistory task={task}/>
                }
                else if(task.status==="run"){
                    return <RunCard task={task} margin/>
                }
            })}
            <CardActions style={{marginLeft:"2%"}}>
            {!!skip && <Button size="small" onClick={()=>{
                        setSkip(skip-10)
                    }}>
                        {"<"}
            </Button>}
            {skip/take+1}
            {(tasksCount)>(skip+tasks.length) && <div>
                <Button size="small" onClick={()=>{
                    setSkip(skip+10)
                }}>
                    {">"}
                </Button>
            </div>}

        </CardActions>
        </div>
    )
}