import { TextField, Box, Button } from "@mui/material";
import { useEffect, useState } from "react";
import { clearExpressCheckStates, expressCheckThunk, precheckCleaning, selectError, selectResponseCategories, selectSelectedCategories } from "../../redux/check/checkSlice";
import { useDispatch, useSelector } from "react-redux";
import AutocompleteCategories from "./AutocompleteCategories";
export default function ExpressCheck(){
    const [borderStyle, setBorderStyle] = useState("solid rgb(0,0,0,0.1)");
    const error = useSelector(selectError);
    const selectedCategories = useSelector(selectSelectedCategories);
    const responseCategories = useSelector(selectResponseCategories)
    const dispatch = useDispatch();
    useEffect(()=>{
        if(error===null){
            setBorderStyle("solid rgb(0,0,0,0.1)")
        }
        else if(error){
            setBorderStyle("solid rgb(255,0,0,0.7)")
        }
        else{
            setBorderStyle("solid rgb(127,255,0,0.5)")
        }
    },[error])
    function check(){
            let productName = document.querySelector("#productName").value
            let categories = selectedCategories.map(cat=>{
                return JSON.parse(cat).code;
            })
            let body = JSON.stringify({
                name:  productName,
                codes: categories
            })
            dispatch(precheckCleaning());
            dispatch(expressCheckThunk(body))
    }
    return (
        <div style={{...expressCheckStyle, border: borderStyle}}>
            <h1 style={{marginLeft:"38%"}}>–ë—ã—Å—Ç—Ä–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞</h1>
            <Box
            component="form"
            sx={{
            '& > :not(style)': { m: 2, display: "inline-block" },
            }}
            noValidate
            autoComplete="off">
                <TextField style={{width:"45%", marginRight:"1%"}} fullWidth id="productName" label="–û–ø–∏—Å–∞–Ω–∏–µ —Ç–æ–≤–∞—Ä–∞" variant="outlined" size="small"/>
                <AutocompleteCategories/>
                <Button onClick={check} variant="contained" color="success">
                    –ü—Ä–æ–≤–µ—Ä–∏—Ç—å
                </Button>
                <Button variant="outlined" color="error" onClick={()=>{
                    document.querySelector("#productName").value  = "";
                    dispatch(clearExpressCheckStates())
                }}>
                    –û—á–∏—Å—Ç–∏—Ç—å
                </Button>
            </Box>    
            {error===true && <p style={{marginLeft:"45%", color:"red"}}>–û—à–∏–±–∫–∞</p>}
            {error===true && responseCategories && <p style={{marginLeft: "1%"}}>–ü—Ä–µ–¥–ø–æ–ª–∞–≥–∞–µ–º—ã–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏: {responseCategories.map(cat=>{
                return cat.name+"; "
            })}</p>}
            {error===false && <p style={{marginLeft:"40%", color:"green"}}>–ü—Ä–æ–≤–µ—Ä–∫–∞ —É—Å–ø–µ—à–Ω–æ –ø—Ä–æ–π–¥–µ–Ω–∞</p>}
        </div>
    )
}

const expressCheckStyle = {
    // border: "solid rgb(127,255,0,0.5)",
    padding: "1%",
    paddingBottom: "1%",
    margin:"1%"
}