import { Button, Box, CircularProgress } from "@mui/material";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getLastTaskThunk, getLastTaskThunkForUpload, selectCurrentTask, selectIsGettingTask, selectIsUploading, sendFileThunk } from "../../redux/check/checkSlice";
import RunCard from "./cards/RunCard";
import SuccesCard from "./cards/SuccessCard";
import ResultTable from "./ResultTable";
export default function FileCheck(){
    const [file, setFile] = useState(null)
    const currentTask = useSelector(selectCurrentTask)
    const isUploading = useSelector(selectIsUploading);
    const isGettingTask = useSelector(selectIsGettingTask);
    const [counter, setCounter] = useState(-100000)
    const dispatch = useDispatch();
    function fileInputHandler (e){
        setFile(e.target.files[0])
    }
    useEffect(()=>{
        dispatch(getLastTaskThunk());
    }, [])
    useEffect(()=>{
        setTimeout(()=>{
            dispatch(getLastTaskThunkForUpload({setCounter, counter}));
        },1000)
    }, [counter])
    function sendFile(){
        let formData = new FormData();
        console.log(file)
        if(file){
            formData.append("file", file);
            dispatch(sendFileThunk({body:formData, fileName:file.name}));
        }
    }
    return(
        <div style={fileCheckStyle}>
            <h1 style={{marginLeft:"38%"}}>–ü—Ä–æ–≤–µ—Ä–∫–∞ —Ñ–∞–π–ª–∞</h1>
            <input multiple="false" type={"file"} onChange={fileInputHandler} id="fileUpload" style={{display:"none"}}/>
            <Box style={{marginBottom:"1%"}}>
                {!isUploading && <Button variant="contained" onClick={()=>{
                    document.querySelector("#fileUpload").click();
                }}>–ó–∞–≥—Ä—É–∑–∏—Ç—å</Button>}
                {isUploading && <div><CircularProgress /> –ò–¥–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∞ —Ñ–∞–π–ª–∞</div>}
                {!!file && <Button onClick={sendFile} variant="text">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</Button>}
            </Box>
            {!!currentTask && !isGettingTask && currentTask.status === "done" && <SuccesCard task={currentTask}/>}
            {!!currentTask && !isGettingTask && currentTask.status === "run" && <RunCard task={currentTask}/>}
            {isGettingTask && <CircularProgress />}
        </div>
    )

}
const fileCheckStyle = {
    border: "solid rgb(0,0,0,0.1)",
    padding: "1%",
    paddingBottom: "1%",
    margin:"1%"
}