import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import { API_URL } from "../../consts"
const initialState = {
    categoriesList:[],
    selectedCategories: [],
    responseCategories: [],
    currentSubTasks: [],
    currentTask: [],
    error:null,
    selectedTaskId: null,
    isUploading:false,
    isGettingTask: true,
    isGettingSubTasks: false,
    isModalResult:false
}
export const getCategoriesThunk = createAsyncThunk(
    "check/getCategoriesThunk",
    async(input)=>{
        let response = await fetch(API_URL+"/api/category_search?like="+input);
        return await response.json();
    }
)

export const expressCheckThunk = createAsyncThunk(
    "check/expressCheckThunk",
    async(body)=>{
        let response = await fetch(API_URL+"/api/express",{
            method: "POST",
            headers:{
                "Content-Type": "application/json"
            },
            body
        })
        return await response.json();
    }
)

export const sendFileThunk = createAsyncThunk(
    "check/sendFileThunk",
    async ({body, fileName})=>{
        let response = await fetch(API_URL+"/api/tasks",{
            method:"POST",
            body
        })
        return await response.json();
    }
)

export const getLastTaskThunk = createAsyncThunk(
    "check/getLastTaskThunk",
    async()=>{
        let response = await fetch(API_URL+"/api/tasks/0");
        return await response.json();
    }

)
export const getSubTasksThunk = createAsyncThunk(
    "check/getSubTasksThunk",
    async({skip, take, id, isValid})=>{
        let isValidStr = isValid ? "&isValid=true" : "&isValid=false";
        if(isValid===undefined) isValidStr = "";
        console.log(isValid)
        let response = await fetch(API_URL+`/api/tasks/${id}?skip=${skip}&take=${take}${isValidStr}`);
        return await response.json();
    }

)
export const getLastTaskThunkForUpload = createAsyncThunk(
    "check/getLastTaskThunkForUpload",
    async({setCounter, counter})=>{
        let response = await fetch(API_URL+"/api/tasks/0");
        setCounter(--counter)
        return await response.json();
    }

)
export const checkSlice = createSlice({
    name: "check",
    initialState,
    ...initialState,
    reducers:{
        setSelectedCategories: (state, action)=>{
            if(!state.selectedCategories.includes(JSON.stringify(action.payload))) state.selectedCategories.push(JSON.stringify(action.payload))
        },
        removeFromSelectedCategories: (state, action) =>{
            state.selectedCategories = state.selectedCategories.filter((e) => e !== action.payload)
        },
        clearCategories: (state)=>{
            state.categoriesList = [];
        },
        setError: (state, action)=>{
            state.error = action.payload
        },
        clearExpressCheckStates: (state)=>{
            state.categoriesList = [];
            state.selectedCategories = [];
            state.error = null;
        },
        precheckCleaning:(state)=>{
            state.responseCategories = [];
            state.error = null;
        },
        setIsModalResult: (state, action)=>{
            state.isModalResult = action.payload;
        },
        setCurrentTask: (state, action)=>{
            state.currentTask = action.payload
        }
    },
    extraReducers: (builder)=>{
        builder.addCase(getCategoriesThunk.fulfilled, (state, action) => {
            // Add user to the state array
            state.categoriesList = action.payload.categories
        });
        builder.addCase(expressCheckThunk.fulfilled,(state, action)=>{
            if(action.payload.isValid===null) state.error = null
            else state.error = !action.payload.isValid
            state.responseCategories = action.payload.—Åategories
            console.log(action.payload.—Åategories)
        });
        builder.addCase(getLastTaskThunk.fulfilled, (state, action)=>{
            state.currentTask = action.payload.task;
            state.isGettingTask = false;
        });
        builder.addCase(getSubTasksThunk.fulfilled, (state, action)=>{
            state.currentSubTasks = action.payload.subTasks ;
            state.isGettingSubTasks = false;
        });
        builder.addCase(getSubTasksThunk.pending, (state, action)=>{
            state.isGettingSubTasks = true;
        });
        builder.addCase(getLastTaskThunkForUpload.fulfilled, (state, action)=>{
            state.currentTask = action.payload.task;
            state.isGettingTask = false;
        });
        builder.addCase(getLastTaskThunk.pending, (state, action)=>{
            state.isGettingTask = true;
        });
        builder.addCase(sendFileThunk.fulfilled, (state,action)=>{
            console.log(action.payload.task);
            state.currentTask = action.payload.task;
            state.isUploading = false
        })
        builder.addCase(sendFileThunk.pending, (state,action)=>{
            state.isUploading = true;
        })
    }

})

export const {setSelectedCategories, removeFromSelectedCategories, clearCategories, clearExpressCheckStates,
                precheckCleaning, setIsModalResult, setCurrentTask} = checkSlice.actions

export const selectCategoriesList = (state) => state.check.categoriesList
export const selectSelectedCategories = (state) => state.check.selectedCategories
export const selectError = (state) => state.check.error
export const selectResponseCategories = (state) => state.check.responseCategories
export const selectCurrentSubTasks = (state) => state.check.currentSubTasks
export const selectCurrentTask = (state) => state.check.currentTask
export const selectSelectedTaskId = (state) => state.check.selectedTaskId
export const selectIsUploading = (state)=> state.check.isUploading
export const selectIsGettingTask = (state)=>state.check.isGettingTask
export const selectIsGettingSubTasks = (state) => state.check.isGettingSubTasks;
export const selectIsModalResult = (state) => state.check.isModalResult

export default checkSlice.reducer